import React from 'react'
import Link from 'gatsby-link'
import styled from "styled-components"
import Hero from '../components/Hero.js'

import { StickyContainer, Sticky } from 'react-sticky';
import Scroll from 'react-scroll';
const scroll = Scroll.animateScroll;


const CourseWrapper = styled.div`
  font-family: 'db_heaventlight';
  font-size: 15px;
  color: #333333;
  letter-spacing: 0;
`

const CourseContent = styled.div`
  position: relative;
`

const HeroWrapper = styled.div`
  position: absolute;
  top: 36%;
  left: 50%;
  transform: translate(-50%);
`

const TabTrigger = styled.div`
  position: absolute;
  bottom: 0;
  li {
    display: inline-block;
    padding: 20px 40px;
    cursor: pointer;
    font-size: 17px;
    color: rgba(255, 255, 255, 0.6);
    text-transform: uppercase;
    letter-spacing: 2.4px;
    line-height: 47px;
  }
  .line {
    background: rgba(255, 255, 255, 0.6);
    width: 100%;
    height: 2px;
    position: relative;
  }
  .indicator {
    width: 25%;
    position: absolute;
    top: 0;
    left: 0;
    height: 2px;
    background: #FFFFFF;
  }
`

const SidebarFixed = styled.div`
  position: absolute;
  z-index: 2;
  top: 50px;
  width: 13% !important;
  left: 10% !important;
  background: #FFFFFF;
  padding: 20px 30px;
  li {
    font-size: 16px;
    color: #333333;
    letter-spacing: 3.2px;
    cursor: pointer;
  }
  li.is-active {
    color: #B83737;
  }
  a {
    background: #B83737;
    padding: 5px 30px;
    font-size: 16px;
    color: #FFFFFF;
    letter-spacing: 3.2px;
    line-height: 35px;
    width: 100%;
    text-decoration: none;
  }
  p {
    margin: 20px 0;
    font-size: 16px;
    color: #333333;
    letter-spacing: 1px;
  }
` 

const ImageFeature = styled.div`
  width: 90px;
  height: 90px; 
  border-radius: 50%;
  background-position: center;
  background-size: cover;
`

const Overview = styled.section`
  padding: 60px 0;
  h4 {
    font-family: 'db_heaventlight';
    font-size: 50px;
    color: #A02424;
    letter-spacing: 6px;
    line-height: 54px;
  }
  ul {
    margin: 30px 0;
  }
  li {
    align-items: center;
  }
  h6 {
    font-family: 'db_heaventbold';
    font-size: 20px;
    color: #333333;
    letter-spacing: 0;
  }
`

const Syllybus = styled.section`
  background-color: #3E3E3E;
  padding: 60px 0;
  color: #FFFFFF;
  h4 {
    font-family: 'db_heaventbold';
    font-size: 40px;
    color: #FFFFFF;
    letter-spacing: 6px;
    line-height: 54px;
  }
  p {
    font-family: 'db_heaventlight';
    font-size: 15px;
    color: #FFFFFF;
    letter-spacing: 0;
    margin: 20px 0;
  }
  h4 + p {
    margin-top: 50px;
  }
  h6 {
    text-transform: uppercase;
    font-size: 28px;
    color: #FFFFFF;
    letter-spacing: 4.56px;
    line-height: 47px;
  }
  li {
    padding-top: 90px;
    counter-increment: li; 
  }
  li:last-child {
    padding-bottom: 90px;
  }
  .head {
    font-size: 18px;
    color: #FFFFFF;
    letter-spacing: 0.9px;
    line-height: 47px;
  }
  .head:after {
    content: counter(li);
    padding: 20px 0;
  }
  li:after {
    content: '';
    width: 40px;
    height: 1px;
    background: #FFFFFF;
    display: block;
  }
`

const Instructors = styled.section`
  padding: 60px 0;
  text-align: center;
  li:before,
  li:last-child:after {
    content: '';
    width: 1px;
    height: 50px;
    margin: 40px auto;
    display: block;
    background: #000000;
  }
  li:first-child:before {
    content: none;
  }
  h4 {
    font-size: 25px;
    color: #333333;
    letter-spacing: 0.9px;
    line-height: 47px;
  }
  h6 {
    font-size: 38px;
    color: #333333;
    letter-spacing: 4.56px;
    line-height: 47px;
  }
  span {
    font-size: 25px;
    color: #333333;
    letter-spacing: 0;
    line-height: 47px;
  }
  p {
    max-width: 500px;
    margin: 0 auto;
  }
`

const Registeration = styled.section`
  .bg {
    height: 400px;
    background-position: center;
    background-size: cover;
  }
  .wrapper {
    padding: 40px 100px;
    max-width: 100%;
  }
  h4 {
    font-size: 55px;
    color: #333333;
    letter-spacing: 11.79px;
    line-height: 47px;
  }
  p {
    font-size: 15px;
    color: #333333;
    letter-spacing: 0;
    max-width: 500px;
  }
  a {
    background: #333333;
    font-size: 16px;
    color: #FFFFFF;
    letter-spacing: 3.2px;
    line-height: 35px;
    padding: 10px 30px;
    text-decoration: none;
  }
`

let i = 0
class Course extends React.Component {
  constructor(props) {
    super(props)

    this.state = {
      data: this.props.data,
      activeName: 'allFormlessJson'
    }
  }

  changeTab(name) {
    this.setState({
      activeName: 'all'+name+'Json'
    })
  }

  jumpTo(id) {
    let posTop = document.getElementById(id).getBoundingClientRect().top
    scroll.scrollTo(posTop + window.pageYOffset, {
      duration: 1000,
      smooth: true,
    });
  }

  render() {

    let data = this.state.data[this.state.activeName].edges[0].node

    return (
      <CourseWrapper>
        <Hero>
          <HeroWrapper>
            <h2>{data.title}</h2>
            <p>{data.desc}</p>
          </HeroWrapper>
          <TabTrigger>
            <div className="line">
              <div className="indicator"></div>
            </div>
            <ul>
              <li onClick={() => this.changeTab('Formless')}>Formless</li>
              <li onClick={() => this.changeTab('Borderless')}>Borderless</li>
              <li onClick={() => this.changeTab('Limitless')}>Limitless</li>
              <li onClick={() => this.changeTab('Timeless')}>Timeless</li>
            </ul>
          </TabTrigger>
        </Hero>
        
        <StickyContainer style={{ position: 'relative' }}>
          <Sticky disableHardwareAcceleration disableCompensation bottomOffset={80}>
            {
              ({ style }) => {
                style.top = style.top + 50
                
                return (
                  <SidebarFixed style={{...style}}>
                    <ul>
                      <li onClick={() => this.jumpTo('overview')}>Overview</li>
                      <li onClick={() => this.jumpTo('syllybus')} className="is-active">Syllybus</li>
                      <li onClick={() => this.jumpTo('instructors')}>Instructor</li>
                      <li onClick={() => this.jumpTo('faq')}>FAQ</li>
                    </ul>

                    <Link to="#">Apply</Link>

                    <p>Talk to admin<br />089-245-3344</p>
                    <p>Office hour<br />Everyday<br />10:00-19:00</p>

                  </SidebarFixed>
                )
              }
            }
          </Sticky>
    

          <Overview className="wrapper" id="overview">
            <h4 dangerouslySetInnerHTML={{ __html: data.overview.title}} />
            <p 
              style={{ maxWidth: '80%' }}
              dangerouslySetInnerHTML={{ __html: data.overview.desc}} />

            <ul>
              {data.overview.courseFor.map((item) => {
                return (
                  <li className="row" key={item.title + Math.random()}>
                    <ImageFeature style={{ backgroundImage: "url(static/hero-bg.png)" }} />
                    <div className="col-lg-10">
                      <h6>{item.title}</h6>
                      <p>{item.desc}</p>
                    </div>
                  </li>
                )
              })}
            </ul>
          </Overview>

          <Syllybus id="syllybus">
            <div className="wrapper">
              <h4>{data.syllybus.title}</h4>
              <p>{data.syllybus.desc}</p>
              
              <ul>
                {data.syllybus.weeks.map((item) => {
                  return (
                    <li key={item.title + Math.random()}>
                      <div className="head">WEEK </div>
                      <h6>{item.title}</h6> 
                      <p>{item.desc}</p>
                    </li>
                  )
                })}
              </ul>
            </div>
          </Syllybus>

          <Instructors id="instructors" className="wrapper">
            <h4>Instructors</h4>
                
            <ul>
              {data.syllybus.weeks.map((item) => {
                return (
                  <li key={item.title + Math.random()}>
                    <h6>{item.title}</h6> 
                    <ImageFeature 
                      style={{ 
                        backgroundImage: "url(static/hero-bg.png)",
                        margin: "20px auto",
                        width: "120px",
                        height: "120px"
                      }} />
                    <span>Know code and design</span> 
                    <p>{item.desc}</p>
                  </li>
                )
              })}
            </ul>   
          </Instructors>

        </StickyContainer>

        <Registeration>
            <div className="bg" style={{ backgroundImage: "url(static/hero-bg.png)" }}></div>
            <div className="wrapper">
              <h4>Registeration</h4>
              <p>{data.desc}</p>
              <Link to="#">APPLY NOW</Link>
            </div>
        </Registeration> 
          
      </CourseWrapper>
    )
  }
}

export default Course

export const query = graphql`
  query Course {
    allFormlessJson {
      edges {
        node {
          title
          desc
          overview {
            title
            desc
            courseFor {
              title
              desc
              imageURL
            }
          }
          syllybus {
            title
            desc
            weeks {
              title
              desc
            }
          }
          instructors {
            name
            excerpt
            desc
            imageURL
          }
        }
      }
    }
    allBorderlessJson {
      edges {
        node {
          title
          desc
          overview {
            title
            desc
            courseFor {
              title
              desc
              imageURL
            }
          }
          syllybus {
            title
            desc
            weeks {
              title
              desc
            }
          }
          instructors {
            name
            excerpt
            desc
            imageURL
          }
        }
      }
    }
  }
`;


