import React from 'react'
import styled from "styled-components"

const HeroWrapper = styled.div`
    background-position: center;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    color: #FFF;
    text-align: center;
    position: relative;
    * {
        position: relative;
        z-index: 2;
    }
    &:after {
        content: '';
        width: 100%;
        height: 100vh;
        background: #000;
        opacity: 0.5;
        position: absolute;
        top: 0;
        left: 0;
    }
    h2 {
        font-family: 'proxima_semibold';
        font-size: 35px;
        color: #FFFFFF;
        letter-spacing: 7.48px;
        line-height: 47px;
        text-transform: uppercase;
        max-width: 700px;
    }
    p {
        font-family: 'db_heaventlight';
        font-size: 15px;
        color: rgba(255, 255, 255, 0.8);
        letter-spacing: 0;
        width: 500px;
        margin: 0 auto;
        padding: 20px 0;
    }
`

class Hero extends React.Component {
    render() {
        return (
            <HeroWrapper style={{ backgroundImage: "url(" + __PATH_PREFIX__ + '/static/hero-bg.png' + ")" }}>
                {this.props.children}
            </HeroWrapper>
        )
    }
}
  
export default Hero
  


